import FormRender from "form-render/lib/antd";
import React, { useState } from "react";
const propsSchema = {
  type: "object",
  properties: {
    college: {
      title: "学院",
      type: "string",
      // "ui:width": "50%",
    },
    metor: {
      title: "辅导员",
      type: "string",
      // "ui:width": "50%",
    },
    name: {
      title: "姓名",
      type: "string",
      // "ui:width": "50%",
    },
    number: {
      title: "学号",
      type: "number",
      // "ui:width": "50%",
    },
    classNumber: {
      title: "班级",
      type: "number",
      // "ui:width": "50%",
    },
  },
};

export default function Demo(props) {
  const [formData, setData] = useState({});
  const [valid, setValid] = useState([]);

  const onSubmit = () => {
    // valid 是校验判断的数组，valid 长度为 0 代表校验全部通过
    if (valid.length > 0) {
      alert(`校验未通过字段：${valid.toString()}`);
    } else {
      // alert(JSON.stringify(formData, null, 2));
      props.handleInput(formData);
    }
  };

  return (
    <div style={{ height: "100%" }}>
      <FormRender
        propsSchema={propsSchema}
        formData={formData}
        onChange={setData}
        onValidate={setValid}
      />
      <button
        onClick={onSubmit}
        style={{
          width: "100%",
        }}
      >
        提交
      </button>
    </div>
  );
}
