import React, { useState, useEffect, useCallback } from "react";
import background from "./assets/zyl.jpg";
import backArrow from "./assets/back.png";
import signImg from "./assets/sign.jpg";
import Form from "./Form";
import "./App.css";

function App() {
  const [currentDate, setCurrent] = useState(new Date());
  const [sign, setSign] = useState(false);
  const [edit, setEdit] = useState(
    () => window.localStorage.getItem("info") === null
  );
  const [info, setInfo] = useState(() => {
    const data = window.localStorage.getItem("info");
    return data === null
      ? {
          college: "电子信息学院",
          metor: "吴晓飞",
          number: 15041633,
          name: "名字",
          classNumber: 15042513,
        }
      : JSON.parse(data);
  });
  useCallback(() => {}, [info]);
  useEffect(() => {
    const timer = setInterval(() => {
      setCurrent(new Date());
    }, 1000);
    return () => clearInterval(timer);
  }, []);
  return (
    <div>
      {edit && (
        <div className="edit">
          <div>点击返回按钮重新呼出此界面</div>
          <Form
            handleInput={(data) => {
              setEdit(false);
              console.log(data);
              setInfo({
                ...info,
                ...data,
              });
              window.localStorage.setItem(
                "info",
                JSON.stringify({
                  ...info,
                  ...data,
                })
              );
            }}
          />
        </div>
      )}
      <div
        style={{
          width: "100vw",
          // height: "484.5px",
          position: "absolute",
          top: 0,
        }}
        id="ball"
      >
        <img
          style={{ zIndex: 1, left: 0, width: "100%" }}
          src={background}
          alt="picture"
        />
      </div>
      <div
        style={{
          zIndex: 10,
          width: "90vw",
          position: "relative",
          top: "133vw",
          fontSize: "16px",
          left: 0,
          fontWeight: 500,
          lineHeight: 1,
        }}
        id="dtime"
      >
        当前时间：{currentDate.toLocaleString()}
      </div>

      <div className="info" style={{ top: "147vw" }} id="name">
        {info.name}
      </div>

      <div className="info" style={{ top: "161vw" }} id="number">
        {info.number}
      </div>

      <div className="info" style={{ top: "176vw" }} id="xueyuan">
        {info.college}
      </div>

      <div className="info" style={{ top: "191vw" }} id="banji">
        {info.classNumber}
      </div>

      <div className="info" style={{ top: "205vw" }} id="teacher">
        {info.metor}
      </div>

      <div className="date" style={{ top: "278vw" }} id="date1">
        {currentDate.getFullYear()}/{currentDate.getMonth() + 1}/
        {currentDate.getDate()}
      </div>
      <div className="date" style={{ top: "311vw" }} id="date2">
        {currentDate.getFullYear()}/{currentDate.getMonth() + 1}/
        {currentDate.getDate()}
      </div>
      <img
        style={{
          zIndex: 10,
          top: "420vw",
          position: "absolute",
          left: "5vw",
          width: "90vw",
        }}
        src={signImg}
        onClick={() => setEdit(true)}
        // onclick="jump()"
      ></img>
      <img
        style={{
          zIndex: 10,
          top: "0px",
          position: "absolute",
          left: "15px",
          width: "10vw",
        }}
        src={backArrow}
        onClick={() => setEdit(true)}
        // onclick="jump()"
      ></img>
    </div>
  );
}

export default App;
